import React, { Component } from 'react';
import Scroll from '../../components/Scroll';

export default class Overview extends Component {
	render() {
		return (
			<div className="page" id="home-overview">
				<Scroll>
					<h2>Willkommen im neuen Heldengenerator und Verwaltungstool fr DSA5!</h2>

					<h3>Viele Anpassungsmglichkeiten</h3>
					<p>
						Die Cha5App hat viele Einstellungen zur Darstellung zu bieten. Dazu zhlen u.A. Sortierfilter. Somit knnt ihr euch die Listen immer so anzeigen lassen, wie ihr sie gerade braucht. Aber auch empfohlene Fhigkeiten zhlen dazu.
					</p>

					<h3>Helden teilen</h3>
					<p>
						Du wollt, dass auch mal jemand anderes einen Blick auf deinen Helden werfen kann, ohne dir ber die Schulter zu schauen? Du wirst seinem Account in einem spteren Patch die Erlaubnis erteilen knnen, dies zu tun. Aber keine Angst, er wird nichts an ihm verndern knnen. OP bleibt OP.
					</p>

					<h3>Gruppen</h3>
					<p>
						Wir werden in einem spteren Patch eine Gruppenverwaltung hinzufgen. Dadurch knnen Gruppenmitglieder bersichtlich angezeigt und verglichen werden. Auerdem kann dieses Tool auch in-game dazu genutzt werden, die INI-Tabelle in Kombination mit Zustands- und Energieanzeigen darzustellen. Diese Ingame-Tabelle hatten wir vorher einmal unter dem Namen "GameApp" verffentlicht. Wir werden dieses Tool fr DSA5 umschreiben.
					</p>
					
					<h3>Ein Wort zur Browserkompatibilitt</h3>
					<p>
						Der Browser wurde mit den neuesten Versionen von Edge, Firefox und Chrome getestet. Die Software ist grundstzlich darauf ausgelegt, dass der jeweilige Browser die Sprachenspezifikation ES5 (ECMAScript 5) vollstndig untersttzt.
					</p>
					
					<h3>Testen</h3>
					<p>
						Du mchtest dir nicht sofort ein Konto erstellen, sondern erstmal die Funktionen testen? Kein Problem! Whle oben rechts einfach "Testen" aus. Du kannst dir damit testweise einen Helden erstellen, allerdings nicht speichern, drucken oder ein Avatarbild hochladen.
					</p>
					<p>
						Wenn du dich dazu entscheidest, ein Konto zu erstellen, kannst du deinen soeben erstellten auch speichern und damit dann alle weiteren Funktionen nutzen. Du darfst vorher nur nicht diese Seite verlassen oder neu geladen haben!
					</p>
				</Scroll>
			</div>
		);
	}
}



